<template>
    <div class="container text-center pluginHome home-gallery">
        <h1 class="heading">v-gallery</h1>

        <p align="center">
            <iframe src="https://ghbtns.com/github-btn.html?user=TerryZ&repo=v-gallery&type=star&count=true" frameborder="0" scrolling="0" width="100px" height="20px"></iframe>
            <iframe src="https://ghbtns.com/github-btn.html?user=TerryZ&repo=v-gallery&type=fork&count=true" frameborder="0" scrolling="0" width="80px" height="20px"></iframe>
        </p>

        <p align="center" class="gallery-container">
            <v-gallery type="carousel" :images="list" ></v-gallery>
        </p>

        <p><i18n path="gallery.projectDesc"></i18n></p>

        <p>
            <a href="https://www.npmjs.com/package/v-gallery" target="_blank">
                <img src="https://img.shields.io/npm/v/v-gallery.svg" alt="v-gallery-npm">
            </a>
            <a href="https://mit-license.org/" target="_blank">
                <img src="https://img.shields.io/badge/license-MIT-brightgreen.svg" >
            </a>
        </p>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                list: [
                    {
                        title: 'Gallery light theme',
                        url: `${this.$store.state.baseImgUrl}/image/v-gallery/v-gallery-light.png`
                    },{
                        title: 'Gallery dark theme',
                        url: `${this.$store.state.baseImgUrl}/image/v-gallery/v-gallery-dark.png`
                    },{
                        title: 'Carousel',
                        url: `${this.$store.state.baseImgUrl}/image/v-gallery/v-gallery-carousel.png`
                    }
                ]
            };
        }
    }
</script>


